import BaseApiService from "../../BaseApiService";

// ---------------- Default path ------------------ //
const BASE_PATH = "/custom/api/";
const DEFAULT_PATH = `${BASE_PATH}legal`;

class LegalService extends BaseApiService {
  updateSummary = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/summary`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };

  updatePropertyDetails = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/property/detail`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };

  updatePropertyAddressAsPerDoc = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/property/address`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };

  updateBoundaryDetails = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/boundary/detail`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };

  updateFinalOpinion = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/final/opinion`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };

  //  /title/of/property
  updateTitleObservation = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/title/of/property`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };

  updateTitleSearch = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/title/search`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };

  updateDocumentsScrutinized = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/document/scrutinised`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };

  updateDocumentsScrutinizedPreDisbursement = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/documentscrutinisedpre/detail`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };

  updateDocumentsScrutinizedPostDisbursement = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/documentscrutinisedpost/detail`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };

  updateDeviations = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/deviation`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };

  getDeviationNames = (start = 0, limit = 10, search: any) => {
    const endPoint = `${BASE_PATH}/deviation/search?start=${start}&limit=${limit}${search}&isActive=true`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  updateDeviationsWithPrePopulate = (legalUid: string) => {
    const endPoint = DEFAULT_PATH + `/autopopulatedeviations/${legalUid}`;
    return this.makePutRequestWithAuth(endPoint);
  };

  getLegalDetails = (stageUid: string) => {
    const endPoint = DEFAULT_PATH + `/search?stage.uid=${stageUid}&isActive=true`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  getOtherProvisionDetails = (stageUid: string) => {
    const endPoint = BASE_PATH + `stage/checklist/search?stage.uid=${stageUid}&isActive=true`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  // createOtherProvisionDetails=(payload:any)=>{
  //   const endPoint = DEFAULT_PATH+`/other/provisions`;
  //   return this.makePostRequestWithAuth(endPoint,payload);
  // };

  updateOtherProvisionDetails = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/other/provisions`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };

  getDocumentExchangeList = (start = 0, limit = 10, search: string) => {
    const endPoint =
      DEFAULT_PATH + `/document/exchange/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  createDocumentExchange = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/document/exchange`;
    return this.makePostRequestWithAuth(endPoint, payload);
  };

  updateDocumentExchange = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/document/exchange`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };

  stageComplete = (stageUid: any) => {
    const endPoint = DEFAULT_PATH + `/complete/${stageUid}`;
    return this.makePutRequestWithAuth(endPoint);
  };

  //  document/exchange/search?loanUid=&stageUid=&loanSectionkey=
  uploadDocument = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/upload/document`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };

  getSavedDocuments = (legalUid: any) => {
    const endPoint = DEFAULT_PATH + `/upload/document/${legalUid}`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  submit = (stageUid: any) => {
    const endPoint = DEFAULT_PATH + `/submit/${stageUid}`;
    return this.makePutRequestWithAuth(endPoint);
  };

  getLegalDeviations = (search: string) => {
    const endPoint = DEFAULT_PATH + `/deviation${search}`;
    return this.makeGetRequestWithAuth(endPoint);
  };
}

export default new LegalService();
